import { client } from "../utils/client";

//   
const getUser = async () => {
	const result = await client
		.get(`/users/me/`)
		.then((response) => response.data)
		.catch((error) => error.response);
	return result;
};

//  
const updateProfile = async (data) => {
	const result = await client
		.put(`/users/`, data)
		.then((response) => response.data)
		.catch((error) => error.response);
	return result;
};
export { getUser, updateProfile };
