import React from "react";
import styled from "styled-components";
import Button from "../../components/common/buttons/Button";
import Introduce from "../../components/common/home/introduce/introduce";
import { Wrapper } from "../../components/styled/Wrapper";
import RecCard from "../../components/common/home/recommendation/RecCard";
import { useNavigate } from "react-router-dom";
import Header from "../../components/layout/Header";
import BackgroundCloud from "../../components/common/home/banner/BackgroundCloud";

const Home = () => {
	const navigate = useNavigate();

	return (
		<>
			<Header></Header>
			<HomeWrapper>
				<HomeBanner>
					<BackgroundCloud></BackgroundCloud>
					<HomeBannerArea>
						<HomeBannerText>  ! </HomeBannerText>
						<HomeBannerText>
							<BoldText></BoldText> &nbsp;  !
						</HomeBannerText>
						<BtnWrapper>
							<PlanBtn></PlanBtn>
						</BtnWrapper>
					</HomeBannerArea>
					<BannerImg src="../assets/main/play.png"></BannerImg>
				</HomeBanner>
				<br />
				<br />
				<MiddleTitle>    - ZZAZO</MiddleTitle>
				<br></br>
				<hr></hr>
				<br />
				<br />
				<IntroduceList>
					<Introduce>
						<ImgWrapper width="7rem" height="7rem" src="../assets/introduce/introduce1.png"></ImgWrapper>
						<Text fontsize="0.7rem">  </Text>
						<Text fontsize="0.7rem">  !</Text>
					</Introduce>
					<Introduce>
						<ImgWrapper width="7rem" height="7rem" src="../assets/introduce/introduce2.png"></ImgWrapper>
						<Text fontsize="0.7rem">     </Text>
						<Text fontsize="0.7rem">    !</Text>
					</Introduce>
					<Introduce>
						<ImgWrapper width="7rem" height="7rem" src="../assets/introduce/introduce3.png"></ImgWrapper>
						<Text fontsize="0.7rem"> </Text>
						<Text fontsize="0.7rem">   !</Text>
					</Introduce>
					<Introduce>
						<ImgWrapper width="7rem" height="7rem" src="../assets/introduce/introduce4.png"></ImgWrapper>
						<Text fontsize="0.7rem">    </Text>
						<Text fontsize="0.7rem">    !</Text>
					</Introduce>
					<Introduce>
						<ImgWrapper width="7rem" height="7rem" src="../assets/introduce/introduce5.png"></ImgWrapper>
						<Text fontsize="0.7rem">  </Text>
						<Text fontsize="0.7rem">   !</Text>
					</Introduce>
				</IntroduceList>
				<RecTitle>  ?</RecTitle>
				<RecArea>
					<CardWrapper>
						<RecCard
							src="../assets/main/location/location1.jpg"
							text=""
							address="  "
							target="20   "
						></RecCard>
						<RecCard
							src="../assets/main/location/location2.jpg"
							text=" "
							address="  "
							target="20   "
						></RecCard>
						<RecCard
							src="../assets/main/location/location3.jpg"
							text=" "
							address="  "
							target="20   "
						></RecCard>
						<RecCard
							src="../assets/main/location/location4.jpg"
							text=" "
							address="  "
							target="20   "
						></RecCard>
					</CardWrapper>
					<CardWrapper>
						<RecCard
							src="../assets/main/location/location5.jpg"
							text=""
							address="  "
							target="20   "
						></RecCard>
						<RecCard
							src="../assets/main/location/location6.jpg"
							text=""
							address="  "
							target="20   "
						></RecCard>
						<RecCard src="../assets/main/location/location7.jpg" text="" address="  " target="20   "></RecCard>
						<RecCard src="../assets/main/location/location8.jpg" text="" address="  " target="20   "></RecCard>
					</CardWrapper>
				</RecArea>

				<BottomArea>
					<BtnWrapper>
						<PlanBtn></PlanBtn>
					</BtnWrapper>
				</BottomArea>

				{/* <SearchPlace></SearchPlace> */}
			</HomeWrapper>
		</>
	);
};

const BottomArea = styled.div`
	display: flex;
	margin: 4rem;
`;

const PlanBtn = styled.button`
	&:hover {
		transform: scale(1.1);
	}
	border-radius: 50px;
	box-shadow: 3px 3px 3px gray;
	position: relative;
	background-color: white;
	border: none;
	font-size: 1rem;
	color: black;
	font-weight: bold;
	padding: 1rem;
	width: 10rem;
	text-align: center;
	-webkit-transition-duration: 0.4s; /* Safari */
	transition-duration: 0.4s;
	text-decoration: none;
	overflow: hidden;
	cursor: pointer;
	@media screen and (max-width: 900px) {
		width: 9rem;
	}
	@media screen and (max-width: 700px) {
		width: 7rem;
	}
	@media screen and (max-width: 500px) {
		width: 5rem;
		font-size: 0.2rem;
	}
`;

const HomeWrapper = styled.div`
	content-direction: column;
	padding-top: 5rem;
	margin: 0 auto;
	width: 70vw;
`;

const CardWrapper = styled.div`
	display: flex;
	@media screen and (max-width: 500px) {
		display: flex;
		flex-direction: column;
	}
`;

const HomeBanner = styled.div`
	width: 100vw;
	margin-left: -21.5%;
	display: flex;
	justify-content: space-evenly;
	background-color: rgba(192, 240, 176, 0.2);
	align-items: center;
	height: 35rem;
	@media screen and (max-width: 500px) {
		display: flex;
		flex-direction: column;
	}
`;

const HomeBannerArea = styled.div`
	display: flex;
	flex-direction: column;
	align-itmes: flex-end;
`;
const HomeBannerText = styled.div`
	font-size: 3.2rem;
	font-weight: 700;
	display: flex;
	align-items: flex-end;
	@media screen and (max-width: 900px) {
		font-size: 3rem;
	}
	@media screen and (max-width: 700px) {
		font-size: 2.5rem;
	}
	@media screen and (max-width: 500px) {
		justify-content: center;
		align-items: center;
		font-size: 2rem;
		display: flex;
		flex-direction: column;
		margin-top: 1rem;
	}
`;

const BannerImg = styled.img`
	&:hover {
		animation: vibration 0.5s infinite;
	}
	@keyframes vibration {
		from {
			transform: rotate(2deg);
		}
		to {
			transform: rotate(-2deg);
		}
	}
	width: 25rem;
	height: 25rem;
	@media screen and (max-width: 1000px) {
		width: 23rem;
		height: 23rem;
	}
	@media screen and (max-width: 800px) {
		width: 20rem;
		height: 20rem;
	}
	@media screen and (max-width: 500px) {
		margin-top: 2rem;
		margin-bottom: 1rem;
		width: 15rem;
		height: 15rem;
	}
`;
const BtnWrapper = styled.div`
	padding-top: 4.5rem;
	margin: 0 auto;
	display: flex;
	@media screen and (max-width: 500px) {
		padding-top: 1rem;
	}
`;
const ImgWrapper = styled.img`
	margin-top: 0.6rem;
	display: flex;
	width: ${({ width }) => width};
	height: ${({ height }) => height};
	margin-bottom: 1.5rem;
	@media screen and (max-width: 900px) {
		width: 6rem;
		height: 6rem;
	}
	@media screen and (max-width: 700px) {
		width: 5rem;
		height: 5rem;
	}
	@media screen and (max-width: 500px) {
		width: 2.5rem;
		height: 2.5rem;
		margin-right: 0.8rem;
		margin-left: 0.8rem;
	}
	@media screen and (max-width: 400px) {
		width: 2rem;
		height: 2rem;
		margin-right: 0.7rem;
		margin-left: 0.7rem;
	}
`;

const MiddleTitle = styled.div`
	margin-top: 2rem;
	display: flex;
	align-items: flex-end;
	font-size: 2rem;
	font-weight: bold;
	@media screen and (max-width: 800px) {
		font-size: 1.7rem;
	}
	@media screen and (max-width: 500px) {
		font-size: 1.3rem;
		justify-content: center;
		align-items: center;
	}
	@media screen and (max-width: 500px) {
		font-size: 1rem;
		justify-content: center;
		align-items: center;
	}
`;

const IntroduceList = styled.div`
	display: flex;
	justify-content: space-evenly;
	align-items: center;
`;

const RecTitle = styled.div`
	padding-top: 1rem;
	text-align: center;
	font-size: 3rem;
	font-weight: 900;
	color: #b7e769;
	margin: 5rem 0;
	@media screen and (max-width: 1000px) {
		font-size: 3.5rem;
	}
	@media screen and (max-width: 800px) {
		font-size: 3rem;
	}
	@media screen and (max-width: 600px) {
		font-size: 2.5rem;
	}
	@media screen and (max-width: 500px) {
		font-size: 2.15rem;
	}
	@media screen and (max-width: 400px) {
		font-size: 1.9rem;
	}
	@media screen and (max-width: 360px) {
		font-size: 1.7rem;
	}
	@media screen and (max-width: 320px) {
		font-size: 1.6rem;
	}
`;

const RecArea = styled.div`
	width: 100vw;
	margin-left: -21.5%;
	display: flex;
	justify-content: space-evenly;
	flex-direction: column;
	background-color: rgba(192, 240, 176, 0.2);
	align-items: center;
	margin-bottom: 5rem;
`;
const Text = styled.p`
	margin-top: 0.4rem;
	font-weight: bold;
	font-size: ${({ fontsize }) => fontsize};
	@media screen and (max-width: 850px) {
		font-size: 1px;
		margin-top: 0;
	}
	@media screen and (max-width: 710px) {
		font-size: 0.01rem;
		margin-top: 0;
	}
`;

const BoldText = styled.p`
	margin-top: 0.7rem;
	font-weight: 900;
	font-size: 3.6rem;
	color: #b7e769;
	@media screen and (max-width: 900px) {
		font-size: 3rem;
	}
	@media screen and (max-width: 700px) {
		font-size: 2.5rem;
	}
	@media screen and (max-width: 500px) {
		margin-top: -0.6rem;
		margin-bottom: 0.3rem;
	}
`;

export default Home;
