import React, { useEffect, useRef, useState } from "react";
import Checkbox from "../../components/common/inputs/Checkbox";
import { ColWrapper, Wrapper } from "./../../components/styled/Wrapper";
import { useNavigate } from "react-router-dom";
import AuthButton from "./../../components/common/buttons/AuthButton";
import { login } from "../../api/AuthAPI";
import { getUser } from "../../api/MyPageAPI";
import Header from "./../../components/layout/Header";
import { AuthInput, AuthWrapper, LogoImage, Option, OptionBorder, Options } from "./../../components/styled/Auth";
import { useDispatch, useSelector } from "react-redux";
import { storeLogin, storeLogout } from "../../store/reducers/user";
import Loading from "./../../components/common/Loading";

const Login = () => {
	// States
	const [state, setState] = useState({
		userEmail: "",
		password: "",
	});
	const [check, setCheck] = useState({
		autoLogin: false,
		idSave: false,
	});
	const [loading, setLoading] = useState(false);

	// Navigate, Ref
	const navigate = useNavigate();
	const idRef = useRef();
	const pwRef = useRef();

	// Dispatch
	const dispatch = useDispatch();

	// Selector
	const userData = useSelector((state) => state.user.value);

	// Effect
	useEffect(() => {
		if (userData.isLogin) {
			if (confirm(" .   ?")) {
				onHandleLogOut();
			}
			return;
		}

		if (sessionStorage.getItem("ACCESS_TOKEN")) {
			sessionStorage.removeItem("ACCESS_TOKEN");
			sessionStorage.removeItem("REFRESH_TOKEN");
			return;
		}

		const loginId = sessionStorage.getItem("LOGIN_ID");
		if (loginId) {
			setState({ ...state, userEmail: loginId });
		}
	}, []);

	const onHandleLogOut = () => {
		dispatch(storeLogout());
		navigate("/");
	};

	const onHandleInput = (e) => {
		setState({ ...state, [e.target.name]: e.target.value });
	};

	const onCheckHandler = (e) => {
		const n = e.target.name;
		setCheck({ ...check, [n]: !check.n });
	};

	const handleOnKeyPress = (e) => {
		if (e.key === "Enter") {
			LoginSubmit();
		}
	};

	//    
	const LoginSubmit = async () => {
		if (state.userEmail.length < 1) {
			alert(" .");
			idRef.current.focus();
			return;
		}
		if (state.password.length < 1) {
			alert(" .");
			pwRef.current.focus();
			return;
		}

		setLoading(true);
		const response = await login(state);
		setLoading(false);

		if (response.status === 200) {
			alert(" !");
		} else if (response.status === 400) {
			alert("  () .");
			return;
		} else if (response.status === 404) {
			alert("   .");
			return;
		} else {
			alert("    .   .");
		}

		// sessionStorage   
		if (check.idSave) {
			sessionStorage.setItem("LOGIN_ID", state.userEmail);
		} else {
			sessionStorage.removeItem("LOGIN_ID");
		}

		//    sessionStorage 
		const accessToken = response.data.token.access;
		const refreshToken = response.data.token.refresh;
		sessionStorage.setItem("ACCESS_TOKEN", accessToken);
		sessionStorage.setItem("REFRESH_TOKEN", refreshToken);

		//     api 
		const userData = await getUser();

		//    dispatch -> data 
		dispatch(storeLogin({ isLogin: true, data: userData }));

		//   
		navigate("/");
	};

	return (
		<>
			<Header />
			<Wrapper>
				{loading ? <Loading /> : null}
				<ColWrapper>
					<AuthWrapper>
						<LogoImage src="/assets/logo.png" />
						<AuthInput
							name="userEmail"
							value={state.userEmail}
							width="80%"
							height="52px"
							placeholder=""
							onChange={onHandleInput}
							onKeyPress={handleOnKeyPress}
							ref={idRef}
						/>
						<AuthInput
							name="password"
							value={state.password}
							width="80%"
							height="52px"
							placeholder=""
							type="password"
							onChange={onHandleInput}
							onKeyPress={handleOnKeyPress}
							ref={pwRef}
						/>
						<Options>
							<Checkbox text=" " tagName="autoLogin" onCheckHandler={onCheckHandler} />
							<Checkbox text=" " tagName="idSave" onCheckHandler={onCheckHandler} />
						</Options>
						<AuthButton message="  " clickEvent={LoginSubmit} />
					</AuthWrapper>
					<Options>
						<Option onClick={() => navigate("/findid")}> </Option>
						<OptionBorder />
						<Option onClick={() => navigate("/findpw")}> </Option>
						<OptionBorder />
						<Option onClick={() => navigate("/signup")}></Option>
					</Options>
				</ColWrapper>
			</Wrapper>
		</>
	);
};

export default Login;
